"use client";

import { useState, useEffect } from 'react';
import { 
  FaProjectDiagram, 
  FaClipboardList, 
  FaCheckCircle, 
  FaTachometerAlt, 
  FaSignOutAlt, 
  FaBars, 
  FaUserFriends, 
  FaSitemap,
  FaUsersCog 
} from 'react-icons/fa';
import Link from 'next/link';
import Image from 'next/image';
import cookies from 'js-cookie';
import { jwtDecode } from 'jwt-decode';

const Sidebar = () => {
  const [isCoordinationOpen, setCoordinationOpen] = useState(false);
  const [isRepartitionOpen, setRepartitionOpen] = useState(false);
  const [isSidebarOpen, setSidebarOpen] = useState(true);
  const [userRole, setUserRole] = useState('');
  const [userId, setUserId] = useState('');

  useEffect(() => {
    const getUserInfo = () => {
      const token = cookies.get('token');
      if (token) {
        try {
          const decoded = jwtDecode(token);
          setUserRole(decoded.role);
          if (decoded.user_id) {
            setUserId(decoded.user_id);
          } else if (decoded.userId) {
            setUserId(decoded.userId);
          }
        } catch (error) {
          console.error('Erreur lors du décodage du token:', error);
        }
      }
    };
    getUserInfo();
  }, []);

  const toggleCoordination = () => setCoordinationOpen(!isCoordinationOpen);
  const toggleRepartition = () => setRepartitionOpen(!isRepartitionOpen);
  const toggleSidebar = () => setSidebarOpen(!isSidebarOpen);

  const isCoordinationAllowed = userRole === 'responsableFiliere';

  return (
    <div 
      className={`h-screen ${isSidebarOpen ? 'w-80' : 'w-20'} bg-[#FFFFFF] text-black p-4 transition-all duration-500 shadow-lg flex flex-col z-30`}
    >
      <div className="flex items-center justify-between mb-1">
        <div className="flex items-center">
          {isSidebarOpen && (
            <Image
              src="/ESTSLOGO24.png"
              alt="School Logo"
              width={300}
              height={150}
              className="object-contain"
              style={{ marginTop: '-60px' }}
            />
          )}
          {!isSidebarOpen && (
            <Image
              src="/ESTSLOGO24.png"
              alt="School Logo"
              width={600}
              height={600}
              className="object-contain"
            />
          )}
        </div>
        <button
          className="md:hidden text-2xl focus:outline-none text-black"
          onClick={toggleSidebar}
          aria-label="Toggle Sidebar"
        >
          <FaBars />
        </button>
      </div>

      <ul className="space-y-2 mt-4">
        <Link href="/dashboard">
          <li className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}>
            <FaTachometerAlt className="text-black" /> {isSidebarOpen && <span className="text-black">Dashboard</span>}
          </li>
        </Link>

        {isCoordinationAllowed && (
          <>
            <li
              className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}
              onClick={toggleCoordination}
            >
              <FaClipboardList className="text-black" /> {isSidebarOpen && <span className="text-black">Coordination</span>}
            </li>

            {isCoordinationOpen && (
              <ul className="ml-5 space-y-2">
                <li
                  className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}
                  onClick={toggleRepartition}
                >
                  <FaSitemap className="text-black" /> {isSidebarOpen && <span className="text-black">Répartition</span>}
                </li>

                {isRepartitionOpen && (
                  <ul className="ml-5 space-y-2">
                    <Link href="/users/readEtu">
                      <li className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300">
                        <FaProjectDiagram className="text-black" /> {isSidebarOpen && <span className="text-black">Gestion Étudiants</span>}
                      </li>
                    </Link>
                    <Link href="/users/readEnca">
                      <li className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300">
                        <FaProjectDiagram className="text-black" /> {isSidebarOpen && <span className="text-black">Gestion Encadrants</span>}
                      </li>
                    </Link>
                    <Link href="/users/readTute">
                      <li className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300">
                        <FaProjectDiagram className="text-black" /> {isSidebarOpen && <span className="text-black">Gestion Tuteur</span>}
                      </li>
                    </Link>
                    <Link href="/users/readSuj">
                      <li className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300">
                        <FaProjectDiagram className="text-black" /> {isSidebarOpen && <span className="text-black">Gestion Sujet</span>}
                      </li>
                    </Link>
                    <Link href="/users/readgenerer">
                      <li className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300">
                        <FaProjectDiagram className="text-black" /> {isSidebarOpen && <span className="text-black">Gestion des groupes</span>}
                      </li>
                    </Link>
                  </ul>
                )}

                <Link href="/users/readaffec">
                  <li className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300">
                    <FaUserFriends className="text-black" /> {isSidebarOpen && <span className="text-black">Affectation</span>}
                  </li>
                </Link>

                <Link href="/users/compte">
                  <li className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300">
                    <FaUsersCog className="text-black" /> {isSidebarOpen && <span className="text-black">Gestion des comptes</span>}
                  </li>
                </Link>
              </ul>
            )}
          </>
        )}

        {userRole === 'tuteur' && userId && (
          <Link href={`/users/Tutinterface/${userId}`}>
            <li className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}>
              <FaCheckCircle className="text-black" /> {isSidebarOpen && <span className="text-black">Consultation</span>}
            </li>
          </Link>
        )}

        {userRole === 'encadrant' && userId && (
          <Link href={`/users/EncadrantInterface/${userId}`}>
            <li className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}>
              <FaCheckCircle className="text-black" /> {isSidebarOpen && <span className="text-black">Consultation</span>}
            </li>
          </Link>
        )}

        {userRole === 'etudiant' && userId && (
          <Link href={`/users/Etinterface/${userId}`}>
            <li className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}>
              <FaCheckCircle className="text-black" /> {isSidebarOpen && <span className="text-black">Consultation</span>}
            </li>
          </Link>
        )}

        <li className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}>
          <FaCheckCircle className="text-black" /> {isSidebarOpen && <span className="text-black">Suivi</span>}
        </li>
        <li className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}>
          <FaCheckCircle className="text-black" /> {isSidebarOpen && <span className="text-black">Soutenance</span>}
        </li>

        <Link href="/logout"> {/* Adjusted to a hypothetical logout route */}
          <li className={`flex items-center gap-3 p-3 hover:bg-[#D2B48C] cursor-pointer transition-all duration-300 ${!isSidebarOpen ? 'justify-center' : ''}`}>
            <FaSignOutAlt className="text-black" /> {isSidebarOpen && <span className="text-black">Déconnexion</span>}
          </li>
        </Link>
      </ul>
    </div>
  );
};

export default Sidebar;